@import '/src/index.scss';

.external-videos {
  display: flex;
  flex-direction: column;
  margin-bottom: 60px;

  .video-filter {
    display: flex;

    .video-filter-left {
      min-width: 50.4%;

      >span:nth-of-type(1) {
        border-bottom: 4px solid #DCDBD6;
        color: #939191;
        margin-top: -12px;
        margin-bottom: 12px;
        text-align: center;
        min-width: 48%;
      }

      .video-filter-list {
        display: flex;
        margin-right: 10%;
        >span {
          border-left: 4px solid #DCDBD6;
          height: 110px;
          margin-top: 30px;
        }
        & ul {
          list-style-type: none;
          z-index: 1;
          margin: 0;
          padding: 0;
          margin-top: 5.5px;
          max-width: 247px;
          flex-grow: 0.55;
          
          >li {
            cursor: pointer;
            display: flex;
            align-items: center;
            padding: 5px 0;
            >span {
              border-bottom: 4px solid #DCDBD6;
              min-width: 40px;
              margin-right: -4px;
            }
            >.polygon svg {
              padding-top: 4px;
              padding-right: 5px;
            }
          }
        }
      }
    }

    .video-filter-right {
      flex: 1;
      .portfolio-description {
        text-align: right;
        border: 4px solid #DCDBD6;
        flex: 0 1 450px;
        padding: 8px 28px 0 0;
        margin-top: -45px;
        z-index: 1;
        p {
          padding: 2px;
        }
      }

      .portfolio-description+span {
        border-top: 4px solid #DCDBD6;
        height: 75px;
        flex: 1;
      }
    }
  }

  >span {
    align-self: center;
    border: 4px solid #FFD029;
    height: 147px;
    margin-top: -160px;
    margin-bottom: 20px;
    min-width: 50%;
  }

  .videos {
    display: flex;
    padding-bottom: 70px;
    flex-wrap: wrap;
    justify-content: center;
    width: 94%;
    margin: 0 auto;
  }

  .videos>* {
    margin: 30px 0 0 30px;
  }
}


@media (max-width: $xl) {
  .external-videos {
    .video-filter {
      flex-wrap: wrap-reverse;

      .video-filter-right {
        .portfolio-description {
          margin: 0 0 0 2%;
          align-self: flex-start;
        }
      }
    }
  }
}

@media (max-width: $sm) {
  .external-videos {
    .video-filter {
      .video-filter-left {
        >span:nth-of-type(1) {
          min-width: 20%;
        }
        >span:nth-of-type(3) {
          display: none;
        }
      }
      .video-filter-right {
        .portfolio-description {
          margin: 0 5%;
        }
      }
    }
    .videos {
      justify-content: flex-start;
    }
  }
}